#skills {
    overflow: hidden;
    width: 100vw;
    max-width: 65rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    margin: 0 auto;
}

.skillTitle {
    font-size: 3rem;
    font-weight: 600;
    padding-top: 3rem;
    margin-bottom: 1.5rem;
}

/* .skillDesc {
    font-weight: 300 ;
    font-size: 1rem;
    max-width: 50rem;
    padding: 0 2rem;
   
} */

.skillBarImg {
    height: 3rem;
    margin-right: 2rem;
}

.skillBars{
    margin: 1.5rem;
    width: 50vw;
    max-width: 80%;
    text-align: left;
}

.skillBar {
    display: flex;
    margin: 1rem;
    padding: 2rem 2rem;
    border-radius: 1.5rem;
    background: rgb(50, 50, 50);
}

@media screen and (max-width:720px) {
    .skillBars{
        margin: 1.5rem;
        width: 90vw;
        max-width: 90%;
        text-align: left;
    }
}

@media screen and (max-width:320px) {
    .skillBars{
        margin: 1.5rem;
        width: 120vw;
        max-width: 100%;
        text-align: left;
    }
}